import {setCss} from "../utils/utils"

export default {
    install(Vue) {
        Vue.directive('drag', {
            bind(el, binding) {
                let dragIndex = 0
                let isTargetDrag = false

                el.addEventListener('mousedown', (ev) => {
                    const {value: {list, index, colors}} = binding
                    const currentBox = document.createElement('div')
                    const {clientX, clientY} = ev, {offsetLeft, offsetTop, parentElement, offsetWidth, offsetHeight, innerHTML} = ev.currentTarget
                    const dx = clientX - offsetLeft, dy = clientY - offsetTop
                    setCss(ev.currentTarget, {
                        backgroundColor : '#fff'
                    })
                    currentBox.className = 'currentBox'
                    currentBox.innerHTML = innerHTML
                    setCss(currentBox, {
                        position: 'absolute',
                        width: `${offsetWidth}px`,
                        height: `${offsetHeight}px`,
                        backgroundColor: colors[index]
                    })
                    parentElement.appendChild(currentBox)
                    const moveBox = document.querySelector('.currentBox')
                    const box = document.querySelectorAll('.dragbox')

                    document.onmousemove = (moveEv) => {

                        let mx = moveEv.clientX - dx,
                            my = moveEv.clientY - dy
                        if (mx < 0) mx = 0
                        if (my < 0) my = 0
                        if (mx > parentElement.offsetWidth - moveBox.offsetWidth) {
                            mx = parentElement.offsetWidth - moveBox.offsetWidth
                        }
                        if (my > parentElement.offsetHeight - moveBox.offsetHeight) {
                            my = parentElement.offsetHeight - moveBox.offsetHeight
                        }

                        list.forEach((item, index) => {
                            const {offsetLeft: ol, offsetTop: ot, offsetWidth: ow, offsetHeight: oh} = box[index]
                            if (mx + ow / 2 > ol &&
                                mx + ow / 2 < ol + ow &&
                                my + oh / 2 > ot &&
                                my + oh / 2 < ot + oh) {
                                dragIndex = index
                                isTargetDrag = true
                                setCss(box[index], {
                                    transform: 'scale(1.1)',
                                })
                            } else {
                                setCss(box[index], {
                                    transform: ''
                                })
                            }
                        })
                        moveBox.style.left = mx + 'px'
                        moveBox.style.top = my + 'px'

                    }
                    document.onmouseup = () => {
                        setCss(box[dragIndex], {backgroundColor: colors[index], transform: ''})
                        if(isTargetDrag){
                            const tempIndex = index, temp = list[tempIndex]
                            list.splice(dragIndex + 1, 0, temp)
                            list.splice(tempIndex, 1)

                        }

                        document.onmousemove = null
                        document.onmouseup = null
                        parentElement.removeChild(moveBox)
                        el.removeEventListener('mousedown', () => {
                        })

                    }
                })
            },
            /*componentUpdated(el, binding) {
                console.log(el,binding);
                el.$list = binding.value.list
            },*/
            /* unbind(el) {
             }*/
            /* inserted(el, binding) {},
             update(el, binding) {},
             componentUpdated() {},
             unbind() {}*/
        })
    }
}
